import $ from "jquery"

$(document).ready(function () {
    $('.tab-btn').click(function () {
        $(".tabSkill").removeClass('tabSkill-active');
        $(".tabSkill[data-id='" + $(this).attr('data-id') + "']").addClass("tabSkill-active");
        $(".tab-btn").removeClass('highlight');
        $(this).addClass('highlight');
    });
});